<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="catalog__item">
    <a class="catalog__pic" href="#" @click.prevent="gotoProductPage">
      <img :src="product.image" :alt="product.title">
    </a>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | formatNumber }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="productColor in product.colors" :key="productColor.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="productColor.id" v-model="selectedColor">
          <span class="colors__value" :style="`background-color: ${productColor.code};`">
          </span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import { PRODUCT_PAGE_NAME } from '@/constants';
import formatNumber from '@/helpers/formatNumber';

export default {
  props: ['product'],
  data() {
    return {
      selectedColor: this.product.colors[0],
    };
  },
  filters: {
    formatNumber,
  },
  methods: {
    gotoProductPage() {
      this.$router.push({ name: PRODUCT_PAGE_NAME, params: { id: this.product.id } });
    },
  },
};
</script>
