<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <div class="form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="productQuantity = Math.max(1, productQuantity - 1)">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="productQuantity" name="count">

    <button type="button" aria-label="Добавить один товар" @click.prevent="productQuantity += 1">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
export default {
  props: ['quantity'],
  data() {
    return { productQuantity: this.quantity };
  },
  watch: {
    productQuantity(value) {
      this.$emit('update:quantity', value);
    },
  },
};
</script>
