<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ item.productId }}
    </span>

    <ProductQuantity :quantity.sync="quantity" />

    <b class="product__price">
      {{ (item.product.price * item.quantity) | formatNumber }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartItem"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import formatNumber from '@/helpers/formatNumber';
import ProductQuantity from './ProductQuantity.vue';

export default {
  components: { ProductQuantity },
  props: ['item'],
  filters: { formatNumber },
  computed: {
    quantity: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        this.$store.dispatch(
          'updateCartItemQuantity',
          { productId: this.item.productId, quantity: value },
        );
      },
    },
  },
  methods: {
    deleteCartItem() {
      this.$store.dispatch('deleteCartItem', this.item.productId);
    },
  },
};
</script>
