<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length }} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="sendOrder">
        <div class="cart__field">
          <div class="cart__data">
            <FormFieldText name="name" placeholder="Введите ваше полное имя" title="ФИО"
                           v-model="formData.name" :error="formErrors.name"/>

            <FormFieldText name="address" placeholder="Введите ваш адрес" title="Адрес доставки"
                           v-model="formData.address" :error="formErrors.address"/>

            <FormFieldText name="phone" placeholder="Введите ваш телефон" title="Телефон" type="tel"
                           v-model="formData.phone" :error="formErrors.phone"/>

            <FormFieldText name="email" placeholder="Введите ваш Email" title="Email" type="email"
                           v-model="formData.email" :error="formErrors.email"/>

            <FormFieldTextarea name="comments" placeholder="Ваши пожелания"
                               title="Комментарий к заказу"
                               v-model="formData.comment" :error="formErrors.comment"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="delivery">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0"
                         checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="delivery">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label" for="pay">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label" for="pay">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="cartItem in products" class="cart__order" :key="cartItem.productId">
              <h3>{{ cartItem.product.title }}</h3>
              <b>{{ cartItem.product.price | formatNumber }} ₽</b>
              <span>Артикул: {{ cartItem.productId }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму <b>{{
                totalPrice | formatNumber
              }}
              ₽</b></p>
          </div>

          <LoaderIndicator v-if="sendingOrder" />
          <button v-else class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="errorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ errorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { API_BASE_URL, ORDER_INFO_PAGE_NAME } from '@/constants';
import formatNumber from '@/helpers/formatNumber';
import { mapGetters } from 'vuex';
import FormFieldText from '@/components/FormFieldText.vue';
import FormFieldTextarea from '@/components/FormFieldTextarea.vue';
import LoaderIndicator from '@/components/LoaderIndicator.vue';
import axios from 'axios';

export default {
  components: {
    FormFieldText,
    FormFieldTextarea,
    LoaderIndicator,
  },
  filters: { formatNumber },
  data() {
    return {
      formData: {},
      formErrors: {},
      errorMessage: '',
      sendingOrder: false,
    };
  },
  computed: {
    ...mapGetters({
      products: 'cartProductsList',
      totalPrice: 'cartTotalPrice',
    }),
  },
  methods: {
    sendOrder() {
      this.formErrors = {};
      this.errorMessage = '';
      this.sendingOrder = true;
      axios.post(
        `${API_BASE_URL}/api/orders`,
        { ...this.formData },
        { params: { userAccessKey: this.$store.state.userAccessKey } },
      ).then((response) => {
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', response.data);
        this.$router.push({ name: ORDER_INFO_PAGE_NAME, params: { id: response.data.id } });
      }).catch((error) => {
        this.formErrors = error.response.data.error.request || {};
        this.errorMessage = error.response.data.error.message;
      }).finally(() => {
        this.sendingOrder = false;
      });
    },
  },
};
</script>
