<!-- eslint-disable max-len -->
<template>
  <main v-if="loading" class="content container">
    <LoaderIndicator />
  </main>
  <main v-else-if="error" class="content container">
    <div>
      <span style="margin-right: 10px;">Произошла ошибка</span>
      <button @click="load">Попробовать снова</button>
    </div>
  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: MAIN_PAGE_NAME }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: CART_PAGE_NAME }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями
            заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li v-for="cartItem in orderInfo.basket.items" class="cart__order" :key="cartItem.id">
              <h3>{{ cartItem.product.title }}</h3>
              <b>{{ cartItem.product.price | formatNumber }} ₽</b>
              <span>Артикул: {{ cartItem.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ orderInfo.basket.items.length }}</b> товара на сумму <b>{{ orderInfo.totalPrice | formatNumber }} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import LoaderIndicator from '@/components/LoaderIndicator.vue';
import { MAIN_PAGE_NAME, CART_PAGE_NAME } from '@/constants';
import formatNumber from '@/helpers/formatNumber';

export default {
  components: { LoaderIndicator },
  filters: { formatNumber },
  data() {
    return { loading: false, error: false };
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      if (this.$store.state.orderInfo?.id === this.$route.params.id) {
        return;
      }

      this.loading = true;
      this.error = false;
      this.$store.dispatch('loadOrderInfo', this.$route.params.id)
        .catch(() => { this.error = true; })
        .finally(() => { this.loading = false; });
    },
  },
  computed: {
    orderInfo() {
      return this.$store.state.orderInfo;
    },
    MAIN_PAGE_NAME() {
      return MAIN_PAGE_NAME;
    },
    CART_PAGE_NAME() {
      return CART_PAGE_NAME;
    },
  },
};
</script>
