/* eslint-disable import/no-extraneous-dependencies */
import OrderInfoPage from '@/pages/OrderInfoPage.vue';
import Vue from 'vue';
import VueRouter from 'vue-router';
import {
  CART_PAGE_NAME,
  MAIN_PAGE_NAME,
  NOT_FOUND_PAGE_NAME, ORDER_INFO_PAGE_NAME, ORDER_PAGE_NAME,
  PRODUCT_PAGE_NAME,
} from '@/constants';
import MainPage from '@/pages/MainPage.vue';
import ProductPage from '@/pages/ProductPage.vue';
import NotFoundPage from '@/pages/NotFoundPage.vue';
import CartPage from '@/pages/CartPage.vue';
import OrderPage from '@/pages/OrderPage.vue';

Vue.use(VueRouter);

const routes = [
  { name: MAIN_PAGE_NAME, component: MainPage, path: '/' },
  { name: PRODUCT_PAGE_NAME, component: ProductPage, path: '/product/:id' },
  { name: CART_PAGE_NAME, component: CartPage, path: '/cart' },
  { name: ORDER_PAGE_NAME, component: OrderPage, path: '/order' },
  { name: ORDER_INFO_PAGE_NAME, component: OrderInfoPage, path: '/order/:id' },
  { name: NOT_FOUND_PAGE_NAME, component: NotFoundPage, path: '*' },
];

export default new VueRouter({ routes });
