<!-- eslint-disable vuejs-accessibility/click-events-have-key-events -->
<!-- eslint-disable max-len -->
<template>
      <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page === 1 }" aria-label="Предыдущая страница" @click.prevent="prevPage">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>
      <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
        <a class="pagination__link" :class="{ 'pagination__link--current': pageNumber === page }" @click.prevent="paginate(pageNumber)">
          {{ pageNumber }}
        </a>
      </li>
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow" :class="{ 'pagination__link--disabled': page === pages }" href="#" aria-label="Следующая страница" @click.prevent="nextPage">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>
</template>

<script>
export default {
  props: ['page', 'total', 'perPage'],
  model: {
    prop: 'page',
    event: 'paginate',
  },
  computed: {
    pages() {
      return Math.ceil(this.total / this.perPage);
    },
  },
  methods: {
    paginate(pageNumber) {
      this.$emit('paginate', pageNumber);
    },
    nextPage() {
      if (this.page === this.pages) {
        return;
      }
      this.$emit('paginate', this.page + 1);
    },
    prevPage() {
      if (this.page === 1) {
        return;
      }
      this.$emit('paginate', this.page - 1);
    },
  },
};
</script>
