<!-- eslint-disable max-len -->
<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :category-id.sync="categoryId" :price-from.sync="priceFrom" :price-to.sync="priceTo"
        :color.sync="colorId" />
      <section class="catalog">
        <UnexpectedError v-if="productsLoadingError" @clicked="loadProducts" />
        <template v-else>
          <LoaderIndicator v-if="productsLoading" />
          <template v-else>
            <ProductsList :products="products" />
            <BasePagination v-model="page" :total="totalProducts" :per-page="productsPerPage" />
          </template>
        </template>
      </section>
    </div>
  </main>
</template>

<script>
import ProductsList from '@/components/ProductsList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import LoaderIndicator from '@/components/LoaderIndicator.vue';
import UnexpectedError from '@/components/UnexpectedError.vue';
import { API_BASE_URL } from '@/constants';
import axios from 'axios';

export default {
  components: {
    UnexpectedError,
    ProductsList,
    BasePagination,
    ProductFilter,
    LoaderIndicator,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,
      priceFrom: 0,
      priceTo: 0,
      categoryId: 0,
      colorId: null,
      productsData: null,
      loadProductsTimerId: undefined,
      productsLoading: false,
      productsLoadingError: false,
    };
  },
  computed: {
    products() {
      // eslint-disable-next-line max-len
      return this.productsData?.items.map((product) => ({ ...product, image: product.image.file.url })) || [];
    },
    totalProducts() {
      return this.productsData?.pagination.total || 0;
    },
  },
  methods: {
    loadProducts() {
      clearTimeout(this.loadProductsTimerId);
      this.productsLoading = true;
      this.productsLoadingError = false;
      this.loadProductsTimerId = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          headers: { 'X-Requested-With': 'XMLHttpRequest' },
          params: {
            page: this.page,
            limit: this.productsPerPage,
            minPrice: this.priceFrom,
            maxPrice: this.priceTo,
            categoryId: this.categoryId,
            colorId: this.colorId,
          },
        })
          .then((response) => { this.productsData = response.data; })
          .catch(() => { this.productsLoadingError = true; })
          .finally(() => { this.productsLoading = false; });
      }, 0);
    },
  },
  created() {
    this.loadProducts();
  },
  watch: {
    page() {
      this.loadProducts();
    },
    categoryId() {
      this.loadProducts();
    },
    colorId() {
      this.loadProducts();
    },
    priceFrom() {
      this.loadProducts();
    },
    priceTo() {
      this.loadProducts();
    },
  },
};
</script>
