/* eslint-disable import/no-extraneous-dependencies */
import { API_BASE_URL } from '@/constants';
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    productsList: [],
    cartProductsData: null,
    userAccessKey: '',
    orderInfo: null,
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.productsList = [];
      state.cartProductsData = null;
    },
    updateCartItemQuantity(state, { productId, quantity }) {
      const cartItem = state.productsList.find((item) => item.productId === productId);
      if (cartItem) {
        cartItem.quantity = quantity;
      }
    },
    updateUserAccessKey(state, userAccessKey) {
      state.userAccessKey = userAccessKey;
    },
    updateCartProductsData(state, cartProductsData) {
      state.cartProductsData = cartProductsData;
    },
    syncProductsList(state) {
      state.productsList = state.cartProductsData.map(
        (item) => ({ productId: item.product.id, quantity: item.quantity }),
      );
    },
  },
  getters: {
    cartProductsList(state) {
      return state.productsList.map((cartItem) => {
        const productDataItem = state.cartProductsData.find(
          (item) => item.product.id === cartItem.productId,
        );
        return {
          ...cartItem,
          product: {
            ...productDataItem.product,
            image: productDataItem.product.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartProductsList.reduce(
        (acc, cartItem) => (cartItem.product.price * cartItem.quantity) + acc,
        0,
      );
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      return axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      return axios.get(`${API_BASE_URL}/api/baskets`, {
        params: { userAccessKey: context.state.userAccessKey },
      })
        .then((response) => {
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }

          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncProductsList');
        });
    },
    addProductToCart(context, { productId, quantity }) {
      return axios.post(
        `${API_BASE_URL}/api/baskets/products`,
        { productId, quantity },
        { params: { userAccessKey: context.state.userAccessKey } },
      )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncProductsList');
        });
    },
    updateCartItemQuantity(context, { productId, quantity }) {
      context.commit('updateCartItemQuantity', { productId, quantity });

      if (+quantity < 1) {
        return;
      }

      // eslint-disable-next-line consistent-return
      return axios.put(
        `${API_BASE_URL}/api/baskets/products`,
        { productId, quantity },
        { params: { userAccessKey: context.state.userAccessKey } },
      )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncProductsList');
        });
    },
    deleteCartItem(context, productId) {
      return axios.delete(
        `${API_BASE_URL}/api/baskets/products`,
        {
          params: { userAccessKey: context.state.userAccessKey },
          data: { productId },
        },
      )
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncProductsList');
        });
    },
  },
});
